import {apiBaseUrl} from "../apiBaseUrl";
import {getError} from "../helper/handlingError";
import {ResponseApiInterface} from "../../../utils/interface";
import {
    REQUEST_MASTER_CATEGORY_RACE,
    REQUEST_MASTER_CATEGORY_RACE_FAILED,
    REQUEST_MASTER_CATEGORY_RACE_SUCCESS
} from "../constant/MasterConstant";

export const fetchAllMasterCategoryRaceAction = () => {
    return async (dispatch: any) => {
        dispatch({
            type: REQUEST_MASTER_CATEGORY_RACE
        });

        try {
            const {data, status}: ResponseApiInterface = await apiBaseUrl({
                method: 'GET',
                headers: {
                    'Content-Type': 'application/json',
                },
                url: '/api/v2/master/category-race',
            })

            if (data.result == null) {
                dispatch({
                    type: REQUEST_MASTER_CATEGORY_RACE_FAILED
                })
            }

            if (status === 200) {
                dispatch({
                    type: REQUEST_MASTER_CATEGORY_RACE_SUCCESS,
                    payload: data
                })
                return data;
            }
        } catch (error) {
            dispatch({
                type: REQUEST_MASTER_CATEGORY_RACE_FAILED,
            });
            await getError(error);
        }
    }
}
