import TitleComponent from "../../../../components/Title/TitleComponent";
import React, {useEffect, useState} from "react";
import {InputFileImage} from "../../../../components/Input/InputFileImage";
import InputText from "../../../../components/Input/InputText";
import Container from "@mui/material/Container";
import SelectBox from "../../../../components/Input/SelectBox";
import {fetchAllMasterCategoryRaceAction} from "../../../../app/service/action/categoryRaceAction";
import {useDispatch, useSelector} from "react-redux";
import {fetchMemberDetail, fetchUpdateMemberDetail} from "../../../../app/service/action/memberAction";
import {useParams} from "react-router-dom";

import {successAlert} from "../../../../app/service/helper/handlingError";
import {DATA_OPTION_GENDER, DATA_OPTION_JERSEY} from "../../../../utils/dummyData";
import Loading from "../../../../components/Loading/Loading";

const INITIAL_ACCOUNT_OBJ = {
    id: 0,
    fullname: '',
    nickname: '',
    photoRider: '',
    gender: '',
    noStart: '',
    dob: '',
    masterCategoryRaceId: '',
    certificateBirtDay: '',
    sizeJersey: ''
}

export const RiderDetail = () => {
    const {id}: any = useParams()
    const dispatch: any = useDispatch();
    const [loading, setLoading] = useState(false);
    const [requestParam, setRequestParams] = useState(INITIAL_ACCOUNT_OBJ)
    const [dataOptionCategory, setDataOptionCategory] = useState([{value: '', name: '--Pilih Kategori--'}]);

    const updateFormValue = ({updateType, value}: any) => {
        // setErrorMessage("")
        setRequestParams({...requestParam, [updateType]: value})
    }

    const updateImageValue = ({updateType, value}: any) => {
        // setErrorMessage("")
        setRequestParams({...requestParam, [updateType]: value})
    }

    const status = useSelector(({masterCategoryRaceState}: any) => {
        return masterCategoryRaceState.masterCategoryRace.status
    });

    const data = useSelector(({masterCategoryRaceState}: any) => {
        return masterCategoryRaceState.masterCategoryRace.data;
    })

    const isLoadingMember = useSelector(({memberDetailState}: any) => {
        return memberDetailState.memberDetail.isLoading;
    })

    const statusMember = useSelector(({memberDetailState}: any) => {
        return memberDetailState.memberDetail.status;
    })

    const dataMember = useSelector(({memberDetailState}: any) => {
        return memberDetailState.memberDetail.data;
    })

    useEffect(() => {
        if (statusMember === 'ready' && dataMember.result) {
            setRequestParams(dataMember.result)
        }
    }, [data.result, dataMember.result, statusMember]);

    useEffect(() => {
        if (status === "ready" && data.result) {
            const update = data.result.map((item: any) => ({
                value: item.id,
                name: item.name
            }))
            setDataOptionCategory(update)
        }
    }, [data.result, status]);

    useEffect(() => {
        // @ts-ignore
        dispatch(fetchAllMasterCategoryRaceAction())
    }, [dispatch]);

    useEffect(() => {
        dispatch(fetchMemberDetail(id))
    }, [dispatch, id]);

    const handleUpdate = () => {
        setLoading(true)

        dispatch(fetchUpdateMemberDetail(requestParam, id))
            .then((response: any) => {
                if (response.result && response.responseData.status === 200) {
                    successAlert("update data sukses!!")
                }

                dispatch(fetchMemberDetail(id))
                setLoading(false)
            }).catch((error: any) => {
            console.log(error)
        })
    }

    return (
        <div id={'detail-rider-page'}>
            <TitleComponent title={'Detail Data Rider'}/>
            <hr/>
            {loading || isLoadingMember ? <Loading/> : (
                <>
                    <Container>
                        <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
                            <div className={'image-account'}>
                                <img src={dataMember.result.photoRider} alt=""/>
                            </div>
                            <InputFileImage
                                updateType={'photoRider'}
                                accept={'image/*'}
                                labelTitle={'Photo Rider'}
                                type={'file'}
                                updateFormValue={updateImageValue}/>
                            <InputText labelTitle={'Nama Lengkap'} updateType={'fullname'}
                                       defaultValue={dataMember.result.fullname}
                                       updateFormValue={updateFormValue}/>
                        </div>
                        <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
                            <InputText labelTitle={'Nama Panggilan'} updateType={'nickname'}
                                       defaultValue={dataMember.result.nickname}
                                       updateFormValue={updateFormValue}/>
                            <SelectBox labelTitle={'Jenis Kelamin'} defaultValue={dataMember.result.gender}
                                       updateFormValue={updateFormValue} updateType={'gender'}
                                       options={DATA_OPTION_GENDER}/>
                            <InputText type={'date'} labelTitle={'Tanggal Lahir'}
                                       updateType={'dob'}
                                       defaultValue={dataMember.result.dob}
                                       updateFormValue={updateFormValue}/>
                        </div>
                        <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
                            <InputText labelTitle={'No. Start/Jersey'} updateType={'noStart'}
                                       defaultValue={dataMember.result.noStart}
                                       updateFormValue={updateFormValue}/>
                            <SelectBox labelTitle={'Category Rider'}
                                       defaultValue={dataMember.result.masterCategoryRaceId}
                                       updateFormValue={updateFormValue} updateType={'masterCategoryRaceId'}
                                       options={dataOptionCategory}/>
                            <SelectBox labelTitle={'Size Jersey Rider'} defaultValue={dataMember.result.sizeJersey}
                                       updateFormValue={updateFormValue} updateType={'sizeJersey'}
                                       options={DATA_OPTION_JERSEY}/>
                        </div>
                        <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
                            <div className={'image-account'}>
                                <img src={dataMember.result.certificateBirtDay} alt=""/>
                            </div>
                            <InputFileImage
                                updateType={'certificateBirtDay'}
                                accept={'image/*'}
                                labelTitle={'Akte Kelahiran'}
                                type={'file'}
                                updateFormValue={updateImageValue}/>
                        </div>
                    </Container>
                    <div className={'button'}>
                        <button className={'btn-update'}
                                onClick={handleUpdate}>
                            UPDATE
                        </button>
                    </div>
                </>
            )}
        </div>
    )
}
